  margin-bottom: 0rem;
  /*reset*/ }

.ProjectCard {
  position: relative;
  width: 100%;
  color: #F2F2F2;
  text-align: center; }

.ProjectCard img {
  opacity: 1;
  /*filter: blur(0px); // 1.5 */ }

.ProjectCard .icons {
  position: absolute;
  width: 100%;
  text-align: center;
  z-index: 11;
  margin-top: -100px; }

.card-intro {
  position: absolute;
  z-index: 10;
  /*text over img*/
  margin-top: 6vw;
  /*8vw ideal für mobil - in the version @media Desktop ändern in 3vw*/
  width: 100%;
  text-align: center;
  font-weight: 700;
  letter-spacing: .1rem;
  text-shadow: 0px 1px 4px #111;
  padding: 5px 0;
  color: #F2F2F2;
  /*(transform: skew(0deg, -10deg);*/
  /*background-color:rgba(255,255,255,0.1);*/ }
  .card-intro span {
    line-height: 2em; }

.card-intro h3 {
  text-align: center;
  font-size: 3.2rem;
  line-height: 1.4em;
  text-transform: uppercase;
  letter-spacing: .3rem; }

.ProjectCard:hover img {
  opacity: 1;
  /*filter: blur(0.5px);*/
  transition: all .4s ease-in-out; }

.underline-anim-1 {
  position: relative;
  text-decoration: none;
  display: inline-block;
  padding: 0 4px;
  /*breite*/
  transition: color ease 0.3s; }
  .underline-anim-1::after {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 0%;
    left: 0;
    bottom: 0;
    background-color: #DC5940;
    transition: all ease 0.4s; }

/*-- hover --*/
.ProjectCard:hover h3 {
  color: #F2F2F2;
  pointer-events: none; }

.ProjectCard:hover .card-intro {
  color: #F2F2F2;
  pointer-events: none; }

.ProjectCard:hover .underline-anim-1 {
  color: white; }
  .ProjectCard:hover .underline-anim-1::after {
    height: 100%; }

